@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.box-img {
  &__holder {
    position: relative;
    width: 100%;
    padding-top: 130%;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}
