:root {
  --rem-base: 10px;
}

* {
  box-sizing: border-box;
  // max-height to all
  max-height: 1000000px;

  &:after,
  &:before {
    box-sizing: inherit;
  }
}

//------------------- transition none when resize
.resize-active {
  transition: none !important;

  *,
  *:before,
  *:after {
    transition: inherit !important;
  }
}

html {
  font-size: var(--rem-base);
}

html,
body {
  height: 100%;
}

body {
  scroll-behavior: smooth;

  @include media('<desktop') {
    padding: 2rem;
  }

  font-family: $base-font-sans-serif;
  color: $color-text;
  background-color: $color-background-900;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: $container-gutter;
  padding-right: $container-gutter;
  max-width: calc(#{$container-with} + #{$container-gutter} * 2);

  @include media('>=desktop') {
    padding-left: 3.2rem;
    padding-right: 3.2rem;
    max-width: calc(#{$container-with} + 3.2rem * 2);
  }
}

/* ******* */

.error {
  text-align: center;
  font-size: 2rem;
  padding: 4.8rem;
}

/* ******* */

.list-watched {
  height: calc(100% - 9rem);
}

/* ******* */

.summary {
  padding: 0 1rem;

  &__holder {
    padding: 1.2rem 2.1rem;
    border-radius: 0.9rem;
    background-color: $color-background-100;
    box-shadow: 0 1.2rem 2.4rem rgba(0, 0, 0, 0.2);

    @include media('>=desktop') {
      padding: 2.2rem 3.2rem 1.8rem 3.2rem1.6rem 3.2rem;
    }
  }

  h2 {
    text-transform: uppercase;
    font-size: 1.6rem;
    margin-bottom: 0.6rem;
  }

  &__box {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 1.2rem;
    font-size: 1.4rem;
    font-weight: 600;

    @include media('>=desktop') {
      gap: 2.4rem;
    }
  }

  p {
    display: flex;
    align-items: center;
    gap: 0.8rem;
  }
}

/* ******* */

.seconds {
  background-color: $color-background-100;
  width: 8rem;
  border-radius: 10rem;
  font-weight: 600;
  text-align: center;
  padding: 0.4rem;
  margin-top: 2.4rem;
}

