@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.logo {
  display: flex;
  align-items: center;
  gap: 0.4rem;

  span {
    font-size: 3.2rem;
  }

  h1 {
    font-size: 2rem;
    font-weight: 600;
    color: $color-tile;

    @include media('<tablet') {
      font-size: 2.4rem;
    }

    @include media('>=desktop') {
      font-size: 2.4rem;
    }
  }
}
