@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.nav-bar {
  @include media('<desktop') {
    border-radius: 0.9rem;
  }

  background-color: $primary-color;

  &__box {
    width: 100%;
    height: 7.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include media('<tablet') {
      flex-direction: column;
      height: auto;
    }
  }

  &__logo {
    padding: 0 2rem;

    @include media('>=tablet') {
      width: 30%;
    }
  }

  &__search {
    @include media('>=tablet') {
      width: 40%;
    }
  }

  &__numreults {
    padding: 0 2rem;
    display: flex;

    @include media('>=tablet') {
      width: 30%;
      justify-content: end;
    }
  }
}
