$breakpoints: (
  'full': 1920px,
  'xl': 1680px,
  'macbook': 1440px,
  'ipad-pro': 1366px,
  'desktop-md': 1200px,
  'desktop': 1024px,
  'tablet': 768px,
  'phone': 667px,
  'phone-md': 568px,
  'phone-sm': 480px,
  'phone-xs': 375px,
);
// ligatured operators ≥ ≤

// keywords
$media-expressions: (
  'iphoneX':
    '(min-width: 812px) and (max-height: 414px) and (orientation: landscape)',
  'screen': 'screen',
  'print': 'print',
  'handheld': 'handheld',
  'landscape': '(orientation: landscape)',
  'portrait': '(orientation: portrait)',
  'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
  'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)',
);

// Colors theme
$primary-color: #6741d9;
$color-primary-light: #7950f2;
$secondary-color: #dee2e6;
$success-color: #808080;
$danger-color: #e03131;
$info-color: #fcd53f;
$fast-color: #2e2554;
$smart-color: #3c3d3f;
$body-color: #424242;
$white-color: #fff;
$light-color: #eee;
$idle-color: #f8f4ff;
$careful-color: #553bf8;
$alt-color: #3d316f;
$other-color: #ffcf49;

$color-background-100: #343a40;
$color-background-500: #2b3035;
$color-background-900: #212529;

$color-tile: $white-color;
$color-text: $secondary-color;
$color-text-dark: $success-color;
$brand-color: #637b8c;
$accent-color: #e25262;

// font-family
$base-font-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
  Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
$base-font-serif: 'Times New Roman', 'Times', 'Baskerville', 'Georgia', serif;

// container size
$container-with: 102.4rem;
$container-gutter: 1rem;
