@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.box {
  width: 100%;
  background-color: $color-background-500;
  border-radius: 0.9rem;
  position: relative;
  overflow: auto;

  @include media('>=tablet') {
    width: 50%;
  }

  &__btn-holder {
    width: 100%;
    padding: 0.8rem;
    display: flex;
    justify-content: flex-end;
  }
}
