@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.list {
  width: 100%;
  list-style: none;
  padding: 0.8rem 0;

  li {
    width: 100%;
    position: relative;
    display: grid;
    grid-template-columns: 4rem 1fr;
    grid-template-rows: auto auto;
    column-gap: 2.4rem;
    font-size: 1.2rem;
    align-items: center;
    padding: 1.2rem 2.1rem;
    border-bottom: 1px solid $color-background-100;

    @include media('>=phone-xs') {
      font-size: 1.6rem;
    }

    @include media('>=desktop') {
      padding: 1.6rem 3.2rem;
    }
  }

  &.list-movies li {
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background-color: $color-background-100;
    }
  }

  .img-holder {
    grid-row: 1 / -1;
  }

  .plug {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    grid-row: 1 / -1;
    width: 4rem;
    height: 5.2rem;
    background: $success-color;
  }

  h3 {
    max-width: 85%;
    font-size: 1.6rem;
    margin: 0 0 0.6rem 0;

    @include media('>=phone-xs') {
      font-size: 1.8rem;
    }
  }

  .info {
    display: flex;
  }

  .date {
    gap: 2.4rem;
    display: flex;
    align-items: center;
  }

  p {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    margin: 0;
  }
}
