@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.details {
  line-height: 1.4;
  font-size: 1.4rem;

  &__header {
    display: flex;
    flex-direction: column;
    align-items: center;

    border-radius: 0.9rem;
    overflow: hidden;
    background-color: $color-background-900;

    @include media('>=phone-md') {
      flex-direction: row;
      align-items: center;
    }
  }

  header {
    padding: 0 1rem;
  }

  section {
    padding: 2rem 1rem;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
  }

  &__img {
    width: 16rem;
    padding: 1rem;
  }

  .plug {
    max-width: 100%;
    background-color: inherit;
  }

  &-overview {
    width: 100%;
    height: 202px;
    padding: 2.4rem 1rem;
    background-color: $color-background-100;
    display: flex;
    flex-direction: column;
    gap: 1.2rem;

    @include media('>=phone-md') {
      width: calc(100% - 16rem);
    }

    @include media('>=desktop') {
      padding: 2.4rem 2rem;
    }
  }

  &-overview h2 {
    font-size: 1.6rem;
    margin: 0;
    line-height: 1.1;

    @include media('>=tablet') {
      font-size: 1.7rem;
    }

    @include media('>=desktop') {
      font-size: 2.4rem;
    }
  }

  &-overview p {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    margin: 0;
  }
}

.rating {
  background-color: $color-background-100;
  border-radius: 0.9rem;
  padding: 2rem 2.4rem;
  margin-bottom: 0.8rem;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
