@import '../../style/modules/include-media.scss';
@import '../../style/modules/variables.scss';

.btn {
  transition: background-color 0.3s;
  cursor: pointer;
  font-weight: bold;
  border: none;

  span {
    display: inline-block;
  }

  &-back {
    position: absolute;
    top: 0.4rem;
    left: 0.8rem;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: $color-background-900;
    color: $secondary-color;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8);
    font-family: sans-serif;
    font-size: 2.4rem;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;

    &:hover {
      background-color: lighten($color-background-900, 10%);
    }

    span {
      margin-top: 0.4rem;
    }
  }

  &-add {
    background-color: $primary-color;
    color: $color-text;
    border-radius: 10rem;
    font-size: 1.4rem;
    padding: 1rem;

    &:hover {
      background-color: lighten($primary-color, 3%);
    }
  }

  &-toggle {
    height: 2.4rem;
    aspect-ratio: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: $color-background-900;
    color: $color-text;
    font-size: 1.4rem;

    &:hover {
      background-color: lighten($color-background-900, 4%);
    }

    span {
      margin-top: -5px;
    }
  }

  &-delete {
    position: absolute;
    height: 1.8rem;
    width: 1.8rem;
    border-radius: 50%;
    right: 1rem;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $info-color;
    color: $color-background-900;
    font-size: 1.6rem;
    transition: background-color 0.3s;

    @include media('>=phone-xs') {
      right: 1.5rem;
    }

    @include media('>=phone-sm') {
      right: 2rem;
    }

    @include media('>=tablet') {
      right: 2.4rem;
    }

    &:hover {
      background-color: darken($info-color, 30%);
    }

    span {
      margin-top: -3px;
    }
  }
}
